/* background */
.popup {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background-color: var(--dark);
    margin-top: 2px;
    display: flex;
    flex: auto;
    justify-content: center;
    align-items: center;
}

/* main popup */
.popup-inner {
    border: 2px solid var(--white);
    padding-bottom: 300px;
    border-radius: 15px;
    position: relative;
    width: 70%;
    background-color: var(--dark);
    padding-bottom: 400px;
}


/* restart text button */
.popup-inner .restart {
    color: var(--titleColor);
    min-width: 18px;
    min-height: 18px;
    position: absolute;
    top: 23px;
    right: 20px;
}

/* new text button */
.popup-inner .next {
    color: var(--titleColor);
    min-width: 25px;
    min-height: 25px;
    position: absolute;
    top: 20px;
    right: 80px;
}

.restart:hover {
    border-bottom: 2px solid white;
}

.next:hover {
    border-bottom: 2px solid white;
}

.popup-title {
    position: absolute;
    color: var(--white);
    font-size: 180%;
    border-bottom: 1px solid;
    border-color: deeppink;
    left: 6.7%;
    margin-top: 15px;
}
/* possible levels */
.popup-sub-title {
    position: absolute;
    color: var(--white);
    font-size: 180%;
    margin-left:50px;
    bottom:130px;
    position:static;
}
.levels{
    position: relative;
    top:100px;
}
.ind-animal{
    width:180px;
    height: 120px;
    position: relative;
    align-items: center;
    justify-content: center;
}

.animal-levels{
    position: relative;
    left:20px;
    bottom:0px;
}

/* achieved level */
.level {
    border: 2px solid;
    border-color: var(--backgroundColor);
    border-radius: 20px;
    position: relative;
    top: 50px;
    left: 60px;
    margin-right: 100px;
}

.animal-img {
    position: relative;
    width: 200px;
    height: 150px;
    border-right: 2px solid;
    padding-right: 20px;
    padding-top: 5px;
    border-color: var(--hotPink);

}

.speed-desc {
    position: relative;
    border: none;
    font-size: 18px;
    text-align: center;
    font-family: "Roboto Mono", "Helvetica Neue", courier new, monospace;
    color: var(--titleColor);
    top: 50px;
    right: 50px;
    margin-bottom: 20px
}

@media screen and (max-width:1795px) {
    .speed-desc {
        font-size: 15px;
    }

}

@media screen and (max-width: 1450px) {
    .speed-desc {
        top: 40px;
        right: 40px;
        font-size: 15px;
    }
    .ind-animal{
        width:150px
    }

}

@media screen and (max-width:1300px) {
    .levels{
        position: relative;
        top:320px;
    }
    .level {
        border: none;
        display: flex;
        flex-direction: column;
        top: 0px;
    }

    .animal-img {
        width: 160px;
        height: 120px;
        border-right: none;
        border-left: 0px solid;
        padding-left: 20px;
        border-bottom: 2px solid;
        border-color: var(--hotPink);
        margin-left: 35%;
        margin-right: 50%;
    }

    .speed-desc {
        top: 150px;
        position: relative;
        left: 0px;
        display: block;
    }
    .ind-animal{
        width:110px;
        height:80px;
    }

}
@media screen and (max-width:1095px){
    .popup-sub-title{
        position: relative;
        bottom:50px;
        border-bottom: 0px
    }
    .animal-levels{
        position: relative;
        bottom:50px;
    }
}
/* phone size */
@media screen and (max-width:959px) {

    .level {
        position: relative;
        top: 20px;
        left: 0px;
        margin-right: 60px;
    }

    .animal-img {
        right: 15px
    }

    .speed-desc {
        position: relative;
        text-align: left;
        margin-left: 70px;
        font-size: 13px
    }
}
@media screen and (max-width: 945px){
    .popup-sub-title{
        bottom:50px;
    }
    .ind-animal{
        width:100px;
        height:60px;
    }
}

@media screen and (max-width:650px) {
    .animal-img {
        width: 140px;
        height: 100px;
    }

    .speed-desc {
        top: 110px;
    }
}

@media screen and (max-width:565px){
    .animal-levels{
        position: relative;
        top:0px;
    }
    .ind-animal{
        width:80px;
        height:50px
    }
    .popup-sub-title{
        top:-20px;
        left:-10px;
    }
}
/** Variables */
:root {
    --background: #323437;
    --dark: #2D2F31;
    --white: #ede8e8;
    --titleColor: #ffdc7a;
    --hotPink:#ed3469
}

/** background color */
body {
    zoom: 1.4;
    background-color: #323437;
    background-color: var(--background);
    box-sizing: border-box;
    align-items: center;
    justify-content: center;
    padding: 0px;
    margin: 0px;
    padding: 10px;
}


/** buttons */
.buttons {
    background-color: #2D2F31;
    background-color: var(--dark);
    border-color: #ede8e8;
    border-color: var(--white);
    position: relative;
    color: #ede8e8;
    color: var(--white);
    padding: .7rem .8rem;
    border-color: #ede8e8;
    border-color: var(--white);
    font-family: "Roboto Mono", sans-serif;
    font-weight: "bold";
    border-radius: 6px;
    top: 3%;
    cursor: pointer;
    outline: 0;
    transition: ease background-color 250ms;
}

/** on hover */
.buttons:hover {
    background-color: #ede8e8;
    background-color: var(--white);
    color: #2D2F31;
    color: var(--dark);
    padding: .7rem .8rem;
}

/** labels for settings */
label {
    text-align: center;
    background-color: #ede8e8;
    background-color: var(--white);
    border-color: #ede8e8;
    border-color: var(--white);
    color: var(--black);
    font-family: "Roboto Mono", sans-serif;
    font-weight: 400;
    border-radius: 4px;
    font-size: 24px;
    padding: 8px 50px 8px 50px;
    margin-bottom: 10px;

}

/** input field */
.inputField {
    position: relative;
    background-color: #2D2F31;
    background-color: var(--dark);
    border-color: #ede8e8;
    border-color: var(--white);
    color: #cccbc9;
    padding-bottom: 13px;
    padding-top: 10px;
    margin-top: 2px;
    font-family: "Roboto Mono", sans-serif;
}

/* on enter */
.inputField:focus {
    outline: none;
    box-shadow: 0 0 1px 1px rgb(19, 18, 18);
    background-color: #2D2F31;
    background-color: var(--dark);
    border-color: #ede8e8;
    border-color: var(--white);
    caret-color: #ffdc7a;
    caret-color: var(--titleColor);
    color: #cccbc9;
    font-family: "Roboto Mono", sans-serif;
}


.quick-type-home-horizontal {
    color: #ffdc7a;
